@use "./SCSS/" as *;

@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

html {
    font-size: 100%;
    scroll-behavior: smooth;

    @include tablet {
        font-size: 80%;
    }

    @include mobile {
        font-size: 60%;
    }
}

body {
    font-family: $font-family;
    font-weight: 400;
    line-height: 1.5;

    background-color: $body-bg;
    color: $txt-color;

    @include mobile {
        margin-bottom: 5rem;
    }
}

a {
    text-decoration: none;
    color: unset;
}

@media (hover: hover) {
    a:hover {
        color: $main-color;
    }
}

button,
input {
    outline: 0;
}

ul {
    list-style-type: none;
}

img {
    max-width: 100%;
}

iframe {
    border: 0;
}

.container {
    padding: 0 2rem;
}

.mb-2 {
    margin-bottom: 1.5rem;
}
.mb-3 {
    margin-bottom: 3rem;
}

.section {
    padding: 0 2rem;
}

.section__header {
    @include flex(center, space-between);

    @include mobile {
        font-size: 1.5rem;
    }
}

.loading {
    width: 100vw;
    height: 100vh;
    font-size: 28px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Toastify__toast-container {
    margin-top: 5rem;
}
